import { useState, useEffect } from 'react';
import { ShoppingCart, Zap, ArrowRight, Loader2, Check, Sparkles, Calendar, MessageSquare, Menu, X } from 'lucide-react';
import { auth, db } from '../lib/firebase';
import { signInWithPopup, GoogleAuthProvider, isSignInWithEmailLink, signInWithEmailLink } from 'firebase/auth';
import { doc, setDoc, serverTimestamp } from 'firebase/firestore';

interface LandingViewProps {
    onLoginSuccess: () => void;
    isLoggedIn?: boolean;
    onGoToDashboard?: () => void;
}

export const LandingView = ({ onLoginSuccess, isLoggedIn = false, onGoToDashboard }: LandingViewProps) => {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    // Handle Email Link Sign-in
    useEffect(() => {
        if (isSignInWithEmailLink(auth, window.location.href)) {
            setIsLoading(true);
            let email = window.localStorage.getItem('emailForSignIn');

            if (!email) {
                email = window.prompt('Please provide your email for confirmation');
            }

            if (email) {
                signInWithEmailLink(auth, email, window.location.href)
                    .then(async (result) => {
                        window.localStorage.removeItem('emailForSignIn');
                        if (result.user) {
                            await setDoc(doc(db, 'users', result.user.uid), {
                                uid: result.user.uid,
                                email: result.user.email,
                                displayName: result.user.displayName || email?.split('@')[0],
                                photoURL: result.user.photoURL,
                                lastLogin: serverTimestamp()
                            }, { merge: true });
                        }
                        onLoginSuccess();
                    })
                    .catch((err) => {
                        console.error("Email link sign-in failed:", err);
                        setError(err.message);
                        setIsLoading(false);
                    });
            } else {
                setIsLoading(false);
                setError("Email is required to complete sign-in.");
            }
        }
    }, [onLoginSuccess]);

    const handleLogin = async () => {
        if (isLoggedIn && onGoToDashboard) {
            onGoToDashboard();
            return;
        }

        setIsLoading(true);
        setError(null);
        try {
            const provider = new GoogleAuthProvider();
            const result = await signInWithPopup(auth, provider);

            if (result.user) {
                await setDoc(doc(db, 'users', result.user.uid), {
                    uid: result.user.uid,
                    email: result.user.email,
                    displayName: result.user.displayName,
                    photoURL: result.user.photoURL,
                    lastLogin: serverTimestamp()
                }, { merge: true });
            }

            onLoginSuccess();
        } catch (err: any) {
            console.error("Login failed:", err);
            if (err.code === 'auth/popup-blocked') {
                setError("Popup blocked! Please allow popups for this site and try again.");
            } else if (err.code === 'auth/configuration-not-found') {
                setError("Firebase Authentication is not configured. Please enable Google Sign-In in the Firebase Console.");
            } else {
                setError(err.message || "Failed to sign in. Please try again.");
            }
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-slate-50 font-sans text-slate-900 overflow-x-hidden">
            {/* Navbar */}
            <nav className="fixed top-0 w-full z-50 bg-white/80 backdrop-blur-md border-b border-slate-200/50">
                <div className="max-w-7xl mx-auto px-6 h-20 flex items-center justify-between">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-gradient-to-br from-indigo-600 to-violet-600 rounded-xl flex items-center justify-center shadow-lg shadow-indigo-200">
                            <Sparkles className="text-white" size={20} />
                        </div>
                        <span className="font-bold text-2xl tracking-tight">Sam</span>
                    </div>

                    {/* Desktop Menu */}
                    <div className="hidden md:flex items-center gap-8">
                        <a href="#features" className="text-sm font-medium text-slate-600 hover:text-indigo-600 transition-colors">Features</a>
                        <a href="#how-it-works" className="text-sm font-medium text-slate-600 hover:text-indigo-600 transition-colors">How it Works</a>
                        <button
                            onClick={handleLogin}
                            className="bg-slate-900 text-white px-5 py-2.5 rounded-full text-sm font-bold hover:bg-slate-800 transition-all hover:scale-105 active:scale-95 shadow-md"
                        >
                            {isLoggedIn ? 'Go to Dashboard' : 'Sign In'}
                        </button>
                    </div>

                    {/* Mobile Menu Toggle */}
                    <button
                        className="md:hidden text-slate-900"
                        onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                    >
                        {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
                    </button>
                </div>

                {/* Mobile Menu */}
                {isMobileMenuOpen && (
                    <div className="md:hidden absolute top-20 left-0 w-full bg-white border-b border-slate-100 p-6 flex flex-col gap-4 shadow-xl">
                        <a href="#features" onClick={() => setIsMobileMenuOpen(false)} className="text-lg font-medium text-slate-900">Features</a>
                        <a href="#how-it-works" onClick={() => setIsMobileMenuOpen(false)} className="text-lg font-medium text-slate-900">How it Works</a>
                        <button
                            onClick={() => {
                                handleLogin();
                                setIsMobileMenuOpen(false);
                            }}
                            className="bg-indigo-600 text-white px-5 py-3 rounded-xl text-lg font-bold w-full"
                        >
                            {isLoggedIn ? 'Go to Dashboard' : 'Sign In'}
                        </button>
                    </div>
                )}
            </nav>

            {/* Hero Section */}
            <header className="relative pt-32 pb-20 lg:pt-48 lg:pb-32 overflow-hidden">
                <div className="absolute inset-0 z-0">
                    <div className="absolute top-0 left-1/2 -translate-x-1/2 w-[1000px] h-[600px] bg-gradient-to-b from-indigo-50 to-white rounded-b-[100px] opacity-60" />
                    <div className="absolute top-20 right-0 w-[400px] h-[400px] bg-purple-200 rounded-full blur-[100px] opacity-20 animate-pulse" />
                    <div className="absolute top-40 left-0 w-[300px] h-[300px] bg-blue-200 rounded-full blur-[100px] opacity-20" />
                </div>

                <div className="max-w-7xl mx-auto px-6 relative z-10 text-center">
                    <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-white border border-slate-200 shadow-sm text-slate-600 text-sm font-semibold mb-8 animate-fade-in-up">
                        <span className="w-2 h-2 bg-green-500 rounded-full animate-pulse" />
                        Sam is now in beta
                    </div>

                    <h1 className="text-5xl lg:text-8xl font-extrabold text-slate-900 tracking-tight mb-8 leading-[1.1]">
                        Meet Sam. <br />
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-indigo-600 via-violet-600 to-purple-600">
                            Your Household AI.
                        </span>
                    </h1>

                    <p className="text-xl lg:text-2xl text-slate-600 mb-10 leading-relaxed max-w-2xl mx-auto">
                        Sam manages your grocery lists, tracks your pantry, and organizes your schedule—all through a simple chat.
                    </p>

                    <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-16">
                        <button
                            onClick={handleLogin}
                            disabled={isLoading}
                            className="h-14 px-8 rounded-full bg-slate-900 text-white font-bold text-lg flex items-center justify-center gap-2 hover:bg-slate-800 transition-all shadow-xl shadow-slate-200 active:scale-95 min-w-[200px]"
                        >
                            {isLoading ? <Loader2 className="animate-spin" /> : (isLoggedIn ? 'Go to Dashboard' : 'Chat with Sam')}
                            {!isLoading && <ArrowRight size={20} />}
                        </button>
                        <a href="#how-it-works" className="h-14 px-8 rounded-full bg-white text-slate-900 border border-slate-200 font-bold text-lg flex items-center justify-center gap-2 hover:bg-slate-50 transition-all">
                            How it Works
                        </a>
                    </div>

                    {error && (
                        <p className="mb-8 text-red-600 text-sm bg-red-50 py-2 px-4 rounded-lg inline-block">{error}</p>
                    )}

                    {/* Hero Visual */}
                    <div className="relative max-w-5xl mx-auto">
                        <div className="relative bg-white rounded-3xl shadow-2xl border border-slate-200 overflow-hidden aspect-[16/9] md:aspect-[2/1] flex flex-col md:flex-row">
                            {/* Chat Interface Mockup */}
                            <div className="flex-1 bg-slate-50 p-6 flex flex-col border-r border-slate-100">
                                <div className="flex items-center gap-3 mb-6">
                                    <div className="w-8 h-8 rounded-full bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                        <Sparkles className="text-white w-4 h-4" />
                                    </div>
                                    <div className="font-bold text-slate-900">Sam</div>
                                </div>
                                <div className="space-y-4 flex-1">
                                    <div className="bg-white p-4 rounded-2xl rounded-tl-none shadow-sm border border-slate-100 max-w-[90%]">
                                        <p className="text-slate-700 text-sm">Good morning! You're out of milk and have a vet appointment at 2 PM. Should I add milk to the shopping list?</p>
                                    </div>
                                    <div className="bg-indigo-600 p-4 rounded-2xl rounded-tr-none shadow-md ml-auto max-w-[90%] text-white">
                                        <p className="text-sm">Yes, please. And add eggs too.</p>
                                    </div>
                                    <div className="bg-white p-4 rounded-2xl rounded-tl-none shadow-sm border border-slate-100 max-w-[90%] animate-pulse">
                                        <div className="flex items-center gap-2">
                                            <Check className="text-green-500 w-4 h-4" />
                                            <p className="text-slate-700 text-sm">Added milk and eggs to your Grocery List.</p>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* App Interface Mockup */}
                            <div className="flex-1 bg-white p-6 hidden md:block">
                                <div className="mb-4">
                                    <div className="text-xs font-bold text-slate-400 uppercase tracking-wider mb-2">Shopping List</div>
                                    <div className="space-y-3">
                                        <div className="flex items-center gap-3 p-3 rounded-xl bg-slate-50 border border-slate-100">
                                            <div className="w-5 h-5 rounded-md border-2 border-slate-300" />
                                            <span className="font-medium text-slate-700">Milk (Whole)</span>
                                            <span className="ml-auto text-xs font-bold text-indigo-600 bg-indigo-50 px-2 py-1 rounded-md">Pantry Empty</span>
                                        </div>
                                        <div className="flex items-center gap-3 p-3 rounded-xl bg-slate-50 border border-slate-100">
                                            <div className="w-5 h-5 rounded-md border-2 border-slate-300" />
                                            <span className="font-medium text-slate-700">Large Brown Eggs</span>
                                        </div>
                                    </div>
                                </div>
                                <div>
                                    <div className="text-xs font-bold text-slate-400 uppercase tracking-wider mb-2">Upcoming</div>
                                    <div className="flex items-center gap-3 p-3 rounded-xl bg-indigo-50 border border-indigo-100">
                                        <div className="w-10 h-10 rounded-lg bg-indigo-200 text-indigo-700 flex items-col items-center justify-center text-xs font-bold leading-none">
                                            <span>Feb</span>
                                            <span className="text-sm">16</span>
                                        </div>
                                        <div>
                                            <div className="font-bold text-slate-900">Vet Appointment</div>
                                            <div className="text-xs text-slate-500">2:00 PM - 3:00 PM</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </header>

            {/* Features Section */}
            <section id="features" className="py-24 bg-white relative z-10">
                <div className="max-w-7xl mx-auto px-6">
                    <div className="text-center max-w-2xl mx-auto mb-16">
                        <h2 className="text-3xl lg:text-4xl font-bold text-slate-900 mb-4">More than just a chatbot.</h2>
                        <p className="text-slate-500 text-lg">Sam is deeply integrated into your household's data, making it actionable and organized.</p>
                    </div>

                    <div className="grid md:grid-cols-3 gap-8">
                        <FeatureCard
                            icon={<MessageSquare className="text-indigo-500" />}
                            title="Chat Anywhere"
                            description="Talk to Sam via WhatsApp or the web app. It's like texting a helpful house manager."
                        />
                        <FeatureCard
                            icon={<ShoppingCart className="text-emerald-500" />}
                            title="Smart Pantry"
                            description="Sam tracks what you buy and what you use. Never question if you have garlic again."
                        />
                        <FeatureCard
                            icon={<Calendar className="text-purple-500" />}
                            title="Family Sync"
                            description="Lists and events are shared with your household instantly. Everyone stays on the same page."
                        />
                    </div>
                </div>
            </section>

            {/* Footer */}
            <footer className="bg-slate-50 py-12 border-t border-slate-200">
                <div className="max-w-7xl mx-auto px-6 flex flex-col md:flex-row justify-between items-center gap-6">
                    <div className="flex items-center gap-2">
                        <div className="w-8 h-8 bg-slate-900 rounded-lg flex items-center justify-center">
                            <Sparkles className="text-white" size={16} />
                        </div>
                        <span className="font-bold text-xl text-slate-900">Sam</span>
                    </div>
                    <p className="text-slate-400 text-sm">© 2026 Sam Household Assistant.</p>
                </div>
            </footer>
        </div>
    );
};

const FeatureCard = ({ icon, title, description }: { icon: React.ReactNode, title: string, description: string }) => (
    <div className="p-8 rounded-3xl bg-slate-50 border border-slate-100 hover:bg-white hover:shadow-xl hover:shadow-slate-200/50 hover:border-slate-200 transition-all duration-300">
        <div className="w-14 h-14 bg-white rounded-2xl shadow-sm border border-slate-100 flex items-center justify-center mb-6">
            {icon}
        </div>
        <h3 className="text-xl font-bold text-slate-900 mb-3">{title}</h3>
        <p className="text-slate-500 leading-relaxed">{description}</p>
    </div>
);
