import React from 'react';
import { Check, Plus, Minus } from 'lucide-react';

interface InventoryItemProps {
    name: string;
    isChecked?: boolean;
    subtext?: string;
    isRecurring?: boolean;
    variant?: 'checkbox' | 'add' | 'quantity';
    quantity?: number;
    onToggle?: () => void; // Used for main click or checkbox
    onIncrement?: () => void;
    onDecrement?: () => void;
}

export const InventoryItem = ({
    name,
    isChecked = false,
    subtext,
    variant = 'checkbox',
    quantity = 1,
    onToggle,
    onIncrement,
    onDecrement
}: InventoryItemProps) => {
    return (
        <div className="bg-white p-3 rounded-xl flex items-center gap-3 shadow-sm border border-transparent hover:border-slate-100 transition-colors cursor-pointer" onClick={onToggle}>

            {/* Action Icon on the Left (Hidden for quantity variant) */}
            {variant !== 'quantity' && (
                <div className="shrink-0 flex items-center justify-center">
                    {variant === 'checkbox' ? (
                        <label className="relative flex items-center justify-center cursor-pointer group">
                            <input
                                type="checkbox"
                                checked={isChecked}
                                onChange={() => { }}
                                onClick={(e) => { e.stopPropagation(); onToggle && onToggle(); }}
                                className="appearance-none w-3 h-3 !min-w-[12px] !min-h-[12px] rounded border border-[var(--accent-sage)] checked:bg-[var(--accent-olive)] checked:border-[var(--accent-olive)] transition-colors"
                            />
                            <Check size={8} className="absolute text-white opacity-0 group-has-[:checked]:opacity-100 pointer-events-none stroke-[3px]" />
                        </label>
                    ) : (
                        <button
                            onClick={(e) => { e.stopPropagation(); onToggle && onToggle(); }}
                            className="w-5 h-5 rounded-full bg-[var(--accent-olive)] flex items-center justify-center text-white hover:bg-[var(--accent-sage)] transition-colors shadow-sm"
                        >
                            <Plus size={12} strokeWidth={3} />
                        </button>
                    )}
                </div>
            )}

            <div className="flex flex-col gap-0.5 flex-1">
                <div className="text-[14px] font-medium text-[var(--text-primary)] leading-tight">{name}</div>
                {subtext && (
                    <div className="text-[10px] text-[var(--text-secondary)] mt-0.5">{subtext}</div>
                )}
            </div>

            {/* Quantity Controls on the Right */}
            {variant === 'quantity' && (
                <div className="flex items-center gap-3 bg-slate-50 rounded-lg p-1" onClick={(e) => e.stopPropagation()}>
                    <button
                        onClick={onDecrement}
                        className="w-6 h-6 flex items-center justify-center rounded-md bg-white border border-slate-200 text-slate-400 hover:text-[var(--accent-orange)] hover:border-[var(--accent-orange)] transition-colors shadow-sm"
                    >
                        <Minus size={12} strokeWidth={3} />
                    </button>

                    <div className="w-4 text-center text-[13px] font-semibold text-[var(--text-primary)]">
                        {quantity}
                    </div>

                    <button
                        onClick={onIncrement}
                        className="w-6 h-6 flex items-center justify-center rounded-md bg-white border border-slate-200 text-slate-400 hover:text-[var(--accent-olive)] hover:border-[var(--accent-olive)] transition-colors shadow-sm"
                    >
                        <Plus size={12} strokeWidth={3} />
                    </button>
                </div>
            )}
        </div>
    );
};
